:root{
  --pageBg: #1d3557;/*#62b6cb;*/
  --mainContainerBg: #edf6f9; /*#bee9e8;*/
  --infoBarColor: white;
  --infoHeaderColor: #dddddd;
  --activeRow: #22577a;
  --shadowColor: #a6a6a6;
}


html{
  font-size: 10px; /*change default font size so 1rem = 10px rather than 16*/
}

body{
  font-family: "Roboto", sans-serif;
  background-color: var(--pageBg);
}

.title{
  display: flex;
  flex-direction: row;
  max-width: 100rem;
  margin: 0 auto 0 auto;
}

.logo{
  height: 6vh;
  margin: 1rem 0 2rem 2rem;
}


.main{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--mainContainerBg);
  border-radius: 2rem;
  overflow: hidden;
  max-width: 100rem;
  height: 80vh;
  margin: 0 auto 0.5rem auto;
  font-size: 1.6rem; /*change font size to 1.6rem to equal browser default of 16px*/
}

.stationInfo{
  width: 100%;
  height: 40%;
}

.infoBar{
  position: relative;
  border-radius: 0.5rem;
  box-shadow: 0.2rem 0.2rem 0.5rem var(--shadowColor);
  background-color: var(--infoBarColor);
  width: 90%;
  height: 25vh;
  overflow-y: auto;
  margin: 2rem auto 1rem auto;
}

.extraHeader{
  display: flex;
  flex-direction: row;
  justify-content: start;
  margin: 0 0 0.5rem 0;
  font-weight: bold;
  font-size: 1.5rem;
}

.extraRow{
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  font-size: 1.2rem;
  margin: 0 0 0.5rem 0;
}

.extraDetails{
  white-space: normal;
  overflow-wrap: break-word;
  width: 100%;
}

.flagIcon{
  margin: 0 1rem 0 0.5rem;
}

.extraCells{
  text-align: left;
}

.map{
  position: relative;
  width: 100%;
  height: 60%; /*10rem*/
}

.callInput{
  width: 100%;
}

.markerInfo{
  position: absolute;
  background-color: var(--infoBarColor);
  border-radius: 0 1.5rem 1.5rem 1.5rem;
  width: 10vw;
  top: 0.2rem;
  left: 2rem;
  margin: 4rem 0 0 0;
  padding: 1rem;
  box-shadow: 0.2rem 0.2rem 0.5rem var(--shadowColor);
}

.callField{
  background-color: var(--infoBarColor);
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: 0 1rem 0 0.5rem;
}

.infoHead {
  position: sticky;
  background-color: var(--infoHeaderColor);
  border-style: none none solid none;
  text-align: left;
  padding: 1rem 1rem 1rem 4rem;
  top: 0;
}

.infoCells {
  text-align: left;
  padding: 1rem 1rem 1rem 4rem;
}

.activeRow{
  text-decoration: none;
  cursor: pointer;
}

.activeRow:hover{
  text-decoration: underline;
  color: var(--activeRow);
}

.callList{
  border-collapse: collapse;
  width: 100%;
  margin: 0 0 0 0;
}

.bottomBar{
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  bottom: 0;
  padding-bottom: 2rem;
}

.footer{
  text-align: center;
  max-width: 100rem;
  margin: 0 auto 0 auto;
  color: white;
}

.clearTable{
  position: relative;
  margin: 0 auto 1rem auto;
}

.popUp{
  position: absolute;
  display: flex;
  flex-direction: column;
  border-style: solid;
  border-width: 0.1rem;
  border-radius: 1rem 1rem 1rem 0;
  background-color: var(--infoBarColor);
  padding: 0.5rem;
  width: 20vw;
  bottom: 100%;
  left: 100%;
}

.popUpButtons{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 1rem auto 0.5rem auto;
}

.routeMain{
  margin: 0 5rem 0 5rem;
}
.menuBar{
  display: flex;
  flex-direction: row;
  margin: auto 0 2rem auto;
}

.menuEl{
  
  margin: auto 2rem 0 2rem;
  font-size: 1.6rem;
}

.options{
  margin: 0 0.5rem 0 0.5rem;
}

a:link{
  color: #ffba08;
}

a:visited {
  color: #f48c06;
}

@media (min-width: 42.1rem) and (max-width: 63rem){

  .logo{
    height: 4vh;
  }

  .markerInfo{
    width: 15vw;
    left: 2rem;
    margin: 4rem 0 0 0;
    padding: 1rem;
  }
    
}

@media (min-width: 29.1rem) and (max-width: 42rem){

  .logo{
    height: 4vh;
  }

  .infoHead {
    font-size: 1.5rem;
    padding: 1rem 1rem 1rem 1rem;

  }

  .infoCells {
    font-size: 1.4rem;
    padding: 1rem 1rem 1rem 1rem;
  }

  .markerInfo{
    width: 22vw;
    left: 2rem;
    margin: 4rem 0 0 0;
    padding: 1rem;
  }

  .popUp{
    width: 35vw;
    bottom: 110%;
    left: 0;
  }

}

@media (min-width: 16.1rem) and (max-width: 29rem){

  .logo{
    height: 4vh;
  }

  .infoHead {
    font-size: 1.3rem;
    padding: 1rem 1rem 1rem 1rem;

  }

  .infoCells {
    font-size: 1.2rem;
    padding: 1rem 1rem 1rem 1rem;
  }

  .markerInfo{
    width: 40vw;
    left: 2rem;
    margin: 4rem 0 0 0;
    padding: 1rem;
  }

  .popUp{
    width: 35vw;
    bottom: 110%;
    left: 0;
  }

  .callField{
    width: 40%;
  }
    
}

@media (max-width: 16rem){

  .logo{
    height: 4vh;
  }

  .infoHead {
    font-size: 1.3rem;
    padding-left: 0.3rem;
    padding-right: 0.3rem;

  }

  .infoCells {
    font-size: 1.2rem;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
  }

  .markerInfo{
    width: 60vw;
    left: 2rem;
    margin: 4rem 0 0 0;
    padding: 1rem;
  }

  .popUp{
    width: 35vw;
    bottom: 110%;
    left: 0;
  }

  .callField{
    width: 40%;
  }
    
}

@media (min-height: 27.1rem) and (max-height: 34rem){

  .infoHead {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;

  }

  .infoCells {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }

}

@media (max-height: 27rem){

  .logo{
    height: 10vh;
    margin: 1rem 0 2rem 2rem;
  }

  .infoHead {
    font-size: 1.4rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;

  }

  .infoCells {
    font-size: 1.3rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }

  .extraHeader{
    font-size: 1.3rem;
  }

  .extraDetails{
    font-size: 1rem;
  }

  .markerInfo{
    padding: 0.5rem;
  }

}